export default {
  data() {
    return {
      formatEn: "DDd HHh mmm sss",
    };
  },
  props: {
    finished:{
      type: Boolean,
      default: false
    },
    tradingList:{
      type: Array,
      default: ()=>{}
    },
    activeType:{
      type: Number,
      default: 0
    }
  },
  methods: {
    swap(swapNow){
      this.$emit('swap', swapNow);
    },
    milliFormat(num) {
      return (
        num &&
        num
          .toString()
          .replace(/^\d+/g, (m) => m.replace(/(?=(?!^)(\d{3})+$)/g, ","))
      );
    },
  }
};
